import React from 'react';
import Transition from 'react-transition-group/Transition';
import {TimelineMax} from 'gsap';
import styled from 'styled-components';
import {
  bool,
  shape,
  arrayOf,
  string
 } from 'prop-types';

/*
 *
 * STYLES
 *
 */

const Card = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1.3);
  width: 110%;
  height: 100%;
  cursor: ${props => props.isHovered ? 'pointer' : 'default'};
  box-shadow: 0.1rem .1rem .8rem rgba(0, 0, 0, .2);
  z-index: 100;
`;

const BackgroundImage = styled.img`
  height: 100%;
  width: 100%;
  object-fit: cover;
`;

const Side = styled.div`
  position: absolute;
  ${props => props.right ? 'right: 0;' : 'left: 0;'}
  top: 0;
  width: 50%;
  height: 100%;
  z-index: 101;
`;

const Details = styled.div`
  position: absolute;
  bottom: 2rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const Dot = styled.span`
  height: 10px;
  width: 10px;
  background-color: ${props => props.color};
  ${'' /* border: 1px solid ${props => props.contrast ? props.color : '#fff'}; */}
  ${props => props.contrast || 'border: 1px solid #fff'};
  border-radius: 50%;
  display: inline-block;
  margin-right: .5rem;
`;

const Price = styled.div`
  margin-right: 1.5rem;
  font-size: 2rem;
  color: ${props => props.contrast ? '#333' : '#fff'};
`;

/*
 *
 * STYLES END
 *
 */

const timeout = 100;

class DetailedView extends React.Component {
  state =  {
    shownImage: ''
  };

  static propTypes = {
    detailedlView: shape({
      hover: arrayOf(string).isRequired,
      price: string.isRequired,
      currency: string.isRequired
    }).isRequired,
    isHovered: bool.isRequired
  };

  setShownImage = (src) => {
    this.setState({shownImage: src});
  };

  emptyShownImage = (src) => {
    this.setState({shownImage: ''});
  };

  render() {
    const {hover, price, currency} = this.props.detailedlView;
    const {isHovered} = this.props;
    const [dollar, cent] = price.split('.');

    return (
      <Transition
        in={isHovered}
        timeout={timeout}
        mountOnEnter={true}
        unmountOnExit={true}
        addEndListener={(elements, done, asd) => {
            const backgroundImage = elements.children[2];
            const details = elements.children[3];
            const dots = details.children[0].children;
            const price = details.children[1];

            const tl = new TimelineMax({
              onComplete: () => console.log('Timeline complete.')
            });

            if (isHovered) {
              tl.from(elements, .3, {
                autoAlpha: 0,
                boxShadow: 0
              })
              .from(backgroundImage, 0, {
                autoAlpha: 0
              })
              .staggerFrom(dots, .2, {
                transform: 'translateY(1rem)',
                autoAlpha: 0
              }, .1)
              .from(price, .3, {
                transform: 'translateY(1rem)',
                autoAlpha: 0
              }, '-=.2');
            } else {
              tl.to(elements, 0, {
                  autoAlpha: 0,
                  boxShadow: 0
                })
                .to(backgroundImage, 0, {
                  autoAlpha: 0,
                  onComplete: this.emptyShownImage
                });
            }
          }}
      >
        <Card isHovered={isHovered}>
          <Side
            onMouseEnter={() => this.setShownImage(0)}
            right
          />
          <Side
            onMouseEnter={() => this.setShownImage(1)}
          />
          <BackgroundImage src={hover[this.state.shownImage]} />
          <Details>
            <div style={{marginLeft: '1.5rem'}}>
              <Dot color='#434c62' contrast={this.state.shownImage === 0}/>
              <Dot color='#092366' contrast={this.state.shownImage === 0}/>
              <Dot color='#A8BEF7' contrast={this.state.shownImage === 0}/>
            </div>

            <Price contrast={this.state.shownImage === 0}>
              <span style={{marginRight: '.5rem'}}>
                {currency}
              </span>
              <span>{dollar},
                <sup style={{
                  verticalAlign: 'top',
                  fontSize: '.6em',
                  position: 'relative',
                  top: '.15em'
                }}>{cent}</sup>
              </span>
            </Price>
          </Details>
        </Card>
      </Transition>
    );
  }
};

export default DetailedView;



