import React from 'react';
import Item from './Item';
import styled from 'styled-components';

import Image from '../img/1.png';
import Hover1 from '../img/hover1.png';
import Hover2 from '../img/hover2.png';

const avgPrice = 119.95;
const currency = '$';
let id = 0;

// At least the prices are different
const getRandomPrice = () => (avgPrice * (0.5 + Math.random())).toFixed(2);

const Grid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  grid-gap: 10rem;
`;

const App = () => {
  return (
    <Grid>
      {
        [...new Array(12)].map(item => (
          <Item key={id++} data={{
            viewSrc: Image,
            detailedlView: {
              hover: [
                Hover1,
                Hover2
              ],
              price: getRandomPrice(),
              currency
            }
          }}
          />
        ))
      }
    </Grid>
  );
};

export default App;



