import React from 'react';
import DetailedView from './DetailedView';
import styled from 'styled-components';
import {
  shape,
  arrayOf,
  string
} from 'prop-types';

const Image = styled.img`
  height: 40rem;
`;

const GridItem = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
`;

class Item extends React.Component {
  state = {
    isHovered: false
  };

  static propTypes = {
    data: shape({
      viewSrc: string.isRequired,
      detailedlView: shape({
        hover: arrayOf(string).isRequired,
        price: string.isRequired,
        currency: string.isRequired
      }).isRequired
    })
  };

  mouseover = (e) => {
    this.setState({isHovered: true});
  };

  mouseleave = (e) => {
    this.setState({isHovered: false});
  };

  render() {
    const {viewSrc, detailedlView} = this.props.data;

    return (
      <GridItem
        onMouseLeave={this.mouseleave}
        onMouseEnter={this.mouseover}
      >
      <Image src={viewSrc} alt='jeans' />
      <DetailedView
        detailedlView={detailedlView}
        isHovered={this.state.isHovered}
      />
      </GridItem>
    );
  }
};

export default Item;



